.App {
  text-align: center;
  font-family: "Poppins", sans-serif;
  background-color: #E2E6E6;
  height: 100%;
  min-height: 100vh;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

/*  */
.btn-1 {
  background-color: #31966C;
  padding: 7px 10px;
  text-decoration: none;
  color: #ECEDE1;
  border-radius: 10px;
  width: 140px;
}

.btn-1:hover {
  background-color: #31966cf1;
  color: #ECEDE1;
}

